.home-container {
    height: 300px;
    color: #e0e6f3;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.home-content {
    max-width: 600px;
    padding: 40px;
    margin-left: 40px;
    text-align: left;

    /* 🔥 Overlay box for readability */
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 12px;
    backdrop-filter: blur(2px); /* Optional glassy effect */
}

.home-content h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    color: #ffffff;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

.home-content h2 {
    font-size: 1.8rem;
    color: #cfc6ff;
    margin-bottom: 30px;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

.home-buttons {
    display: flex;
    gap: 15px;
}

.home-buttons .btn-hire,
.home-buttons .btn-cv {
    padding: 12px 24px;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
}

.home-buttons .btn-hire {
    background-color: #5a7fff;
    color: white;
    box-shadow: 0 4px 12px rgba(90, 127, 255, 0.6);
}

.home-buttons .btn-hire:hover {
    background-color: white;
    color: #5a7fff;
    transform: scale(1.08);
    box-shadow: 0 6px 18px rgba(90, 127, 255, 0.8);
    border: 1px solid #5a7fff;
}

.home-buttons .btn-cv {
    background-color: #7c6ff5;
    color: white;
    box-shadow: 0 4px 12px rgba(124, 111, 245, 0.6);
}

.home-buttons .btn-cv:hover {
    background-color: white;
    color: #7c6ff5;
    transform: scale(1.08);
    box-shadow: 0 6px 18px rgba(124, 111, 245, 0.8);
    border: 1px solid #7c6ff5;
}
