.sidebar {
    height: 100vh;
    width: 220px;
    background-color: #0d0d1f; /* darker black-blue tone */
    position: fixed;
    box-shadow: 2px 0 15px rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    transition: width 0.3s ease;
}

.sidebar.collapsed {
    width: 60px;
}

/* Toggle icon container */
.sidebar-toggle-icons {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    padding: 10px;
}

/* Toggle icon */
.sidebar-toggle-icons p svg {
    background-color: #5a7fff; /* neon blue */
    color: white;
    padding: 5px;
    border-radius: 50%;
    transition: all 0.3s ease;
    box-shadow: 0 4px 10px rgba(90, 127, 255, 0.5);
}

.sidebar-toggle-icons p svg:hover {
    background-color: white;
    color: #5a7fff;
    transform: scale(1.1);
}

/* Optional: collapsed sidebar hover tooltip effect (if you add titles/icons) */
.sidebar.collapsed .menu-item-text {
    display: none; /* hide text when collapsed */
}

/* Optional menu link styling for consistency */
.sidebar a {
    color: #e0e6f3;
    text-decoration: none;
        transition: color 0.3s ease;
}

.sidebar a:hover {
    color: #9a91f5; /* soft violet hover */
}

.footer {
  width: 100%;
  background-color: #0d0d1f; /* same dark black-blue tone as sidebar */
  color: #e0e6f3; /* light text matching sidebar links */
  box-shadow: 0 -2px 15px rgba(0, 0, 0, 0.6); /* subtle shadow from top */
  padding: 15px 0;
  text-align: center;
  font-weight: 500;
  font-size: 1rem;
  user-select: none;
  /* if footer fixed at bottom */
  /* position: fixed;
     bottom: 0;
     left: 0; */
}
