.education {
  padding: 10px 20px;
  background-color: #0a0a0a;
  color: #e0e6f3;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.education h2 {
  color: #a99bf7;
  margin-bottom: 0;
  font-weight: bold;
  text-align: center;
  font-size: 2.2rem;
  text-shadow: 1px 1px 5px rgba(154, 145, 245, 0.5);
}

.education hr {
  border: none;
  height: 2px;
  width: 100px;
  background-color: #a99bf7;
  margin: 20px auto;
}

.education-container {
  padding: 3rem 1rem;
  max-width: 800px;
  margin: 0 auto;
}

.section-title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #a99bf7;
  font-weight: bold;
  text-shadow: 1px 1px 4px rgba(154, 145, 245, 0.4);
}

.education-timeline {
  position: relative;
  margin-left: 20px;
  border-left: 3px solid #a99bf7;
  padding-left: 20px;
}

.timeline-item {
  position: relative;
  margin-bottom: 2rem;
  padding-left: 1.5rem;
}

.timeline-icon {
  position: absolute;
  left: -35px;
  top: 0;
  background: #a99bf7;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-size: 1rem;
  box-shadow: 0 0 8px rgba(154, 145, 245, 0.7);
}

.timeline-content {
  background: #181818;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  border-left: 4px solid #a99bf7;
  transition: transform 0.3s ease;
}

.timeline-content:hover {
  transform: scale(1.02);
}

.timeline-content h3 {
  margin: 0;
  font-size: 1.3rem;
  color: #ffffff;
}

.timeline-content h4 {
  margin: 0.4rem 0;
  font-weight: 500;
  color: #ccc;
}

.timeline-date {
  font-size: 0.9rem;
  color: #a99bf7;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}
