.contact-section {
  background-color: rgba(10, 10, 10, 0.75);
  color: #e0e6f3;
  padding: 60px 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.contact-container {
  display: flex;
  flex-direction: column;
  background: #151528;
  max-width: 1000px;
  margin: auto;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
}

@media (min-width: 768px) {
  .contact-container {
    flex-direction: row;
  }
}

.contact-info-panel {
  background-color: #1c1c34;
  flex: 1;
  padding: 40px;
  color: #cfc6ff;
}

.contact-info-panel h2 {
  color: #a99bf7;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.contact-info-panel p {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.contact-detail p {
  margin: 0.3rem 0;
  font-size: 0.95rem;
  color: #aaa;
}

/* Social links container */
.social-links {
  margin-top: 1.5rem;
  display: flex;
  gap: 20px;
}

/* Social icon links */
.social-links a {
  color: #a99bf7; /* violet tone matching headings */
  font-size: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-links a.Github:hover {
  color: black; /* GitHub green */
  transform: scale(1.2);
}

.social-links a.Linkedin:hover {
  color: #0077b5; /* LinkedIn blue */
  transform: scale(1.2);
}

.contact-form {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 40px;
  background-color: #121223;
}

.contact-form input,
.contact-form textarea {
  background: #1e1e35;
  border: 1px solid #2e2e50;
  color: #f1f1f1;
  padding: 12px 16px;
  font-size: 1rem;
  border-radius: 10px;
  transition: border 0.3s ease;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  border: 1px solid #a99bf7;
}

.contact-form button {
  background: #a99bf7;
  color: #fff;
  padding: 12px 24px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.contact-form button:hover {
  background: #fff;
  color: #a99bf7;
  border: 1px solid #a99bf7;
  transform: translateY(-2px);
}
