/* ExperienceAccordion.css */
.experience-accordion {
  max-width: 900px;
  margin: 3rem auto;
  padding: 0 1rem;
  padding: 10px 20px;
  /* background-color: #0a0a0a; */
  color: #e0e6f3;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #e0e6f3;
  /* background: #0d0d1f; */
  border-radius: 12px;
  /* box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5); */
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  color: #a99bf7;
  margin-bottom: 2rem;
  font-weight: 700;
  text-shadow: 1px 1px 6px rgba(169, 155, 247, 0.3);
}

.accordion-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.accordion-item {
  background: #1a1a2e;
  border-radius: 10px;
  overflow: hidden;
  transition: box-shadow 0.3s ease;
  box-shadow: 0 0 0 transparent;
  border-left: 4px solid transparent;
}

.accordion-item.expanded {
  box-shadow: 0 6px 15px rgba(169, 155, 247, 0.4);
  border-left: 4px solid #a99bf7;
}

.accordion-header {
  background: transparent;
  border: none;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem;
  cursor: pointer;
  color: #e0e6f3;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: left;
  gap: 1rem;
  outline: none;
  transition: background 0.3s ease;
}

.accordion-header:hover {
  background: rgba(169, 155, 247, 0.1);
}

.icon {
  font-size: 1.8rem;
  color: #a99bf7;
  min-width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-text h3 {
  margin: 0;
  font-weight: 700;
  color: #a99bf7;
  text-shadow: 1px 1px 2px rgba(169, 155, 247, 0.3);
}

.header-text p {
  margin: 2px 0 0 0;
  font-weight: 400;
  color: #b0b7cc;
  font-size: 0.9rem;
}

.duration {
  margin-left: auto;
  font-weight: 500;
  color: #a0a8c0;
  font-size: 0.85rem;
  white-space: nowrap;
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  padding: 0 1.5rem;
  background: #151524;
  color: #d6d7e0;
  font-weight: 400;
  font-size: 1rem;
  transition: max-height 0.35s ease, padding 0.35s ease;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.accordion-item.expanded .accordion-content {
  max-height: 500px;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
}

.accordion-content ul {
  margin: 0;
  padding-left: 1.3rem;
  list-style-type: disc;
}

.accordion-content li {
  margin-bottom: 0.6rem;
  line-height: 1.5;
  color: #c7cce4;
}
