.about-content-wrapper {
  display: flex;
  gap: 10px;
  width: 100%;
  background-color: rgba(10, 10, 10, 0.75);
  padding: 10px;
  border-radius: 15px;
  /* box-shadow:  6px 20px rgba(0, 0, 0, 0.8); */
  color: #e0e6f3;
  align-items: stretch;
}

.about-img {
  flex: 0 0 auto;
  width: 40vw;
  max-width: 250px;
  min-width: 100px;

  /* Add padding inside the container */
  padding: 40px 20px;  /* vertical and horizontal padding */
  
  /* Make it a flex container to center image vertically */
  display: flex;
  justify-content: center;  /* horizontally center */
  align-items: center;      /* vertically center */
}

.about-img img {
  width: 100%;
  height: auto; /* keep aspect ratio */
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 4px 12px rgba(90, 127, 255, 0.5);
  display: block;
}

.about-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-content h1 {
  color: #9a91f5;
  font-family: cursive;
  border-bottom: 2px solid #9a91f5;
  margin-bottom: 15px;
}

.about-content p {
  line-height: 1.45;
  margin-bottom: 15px;
  color: #d1d4e0;
  text-align: justify;
}

/* Responsive */
@media (max-width: 768px) {
  .about-content-wrapper {
    flex-direction: column;
    padding: 20px;
  }

  .about-img {
    width: 100%;
    max-width: 100%;
    margin-bottom: 20px;
    padding: 20px 0; /* adjust padding for small screens */
    display: block;
  }

  .about-img img {
    height: auto;
    width: 100%;
  }
}
