.project {
  padding: 10px 20px;
  background-color: #0a0a0a;
  color: #e0e6f3;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.project h2 {
  color: #a99bf7;
  margin-bottom: 30px;
  font-weight: bold;
  text-align: center;
  font-size: 2.2rem;
  text-shadow: 1px 1px 5px rgba(154, 145, 245, 0.5);
}

/* ===== Tabs ===== */
.project-tabs {
  text-align: center;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 12px;
}

.tab-btn {
  background-color: transparent;
  color: #9a91f5;
  border: 2px solid #9a91f5;
  padding: 8px 20px;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-shadow: 0 0 3px rgba(154, 145, 245, 0.7);
}

.tab-btn.active,
.tab-btn:hover {
  background-color: #9a91f5;
  color: #0a0a0a;
  text-shadow: none;
}

/* ===== Card Layout ===== */
#ads {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

#ads .col-md-4 {
  width: calc(33.333% - 20px);
  display: flex;
  justify-content: center;
}

.card {
  background-color: #121212;
  border: none;
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: 430px;  /* fixed uniform height */
  width: 300px;   /* fixed uniform width */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #e0e6f3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 24px rgba(124, 111, 245, 0.4);
}

.card-image {
  position: relative;
  aspect-ratio: 4 / 3;
  overflow: hidden;
  flex-shrink: 0;
}

.card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.card-notify-badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #5a7fff;
  color: white;
  padding: 5px 5px;
  border-radius: 8px;
  font-size: 0.8rem;
  font-weight: bold;
  box-shadow: 0 2px 6px rgba(90, 127, 255, 0.5);
}

.card-image-overly {
  padding: 5px 10px;
  text-align: center;
  flex-wrap: wrap;
}

.card-detail-badge {
  background-color: #7c6ff5;
  color: white;
  padding: 5px 5px;
  border-radius: 5px;
  font-size: 0.75rem;
  margin: 3px;
  display: inline-block;
}

.card-body {
  padding: 10px;
  text-align: center;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-body h5 {
  color: #ffffff;
  margin-bottom: 5px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

.ad-btn {
  display: inline-block;
  padding: 10px 10px;
  background-color: transparent;
  color: #9a91f5;
  border: 2px solid #9a91f5;
  border-radius: 25px;
  font-weight: 600;
  transition: all 0.3s ease;
  text-decoration: none;
  cursor: pointer;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-shadow: 0 4px 12px rgba(154, 145, 245, 0.6);
}

.ad-btn:hover {
  background-color: #9a91f5;
  color: #0a0a0a;
  box-shadow: 0 6px 18px rgba(154, 145, 245, 0.8);
  transform: scale(1.05);
}

/* ===== Responsive ===== */
@media (max-width: 992px) {
  #ads .col-md-4 {
    width: calc(50% - 20px);
  }
}

@media (max-width: 576px) {
  #ads .col-md-4 {
    width: 100%;
  }

  .project-tabs {
    flex-direction: column;
    align-items: center;
  }
}
